import React from 'react'

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'

function GetQuote(props) {
    return (
        <div id="wrapper">
            <FontAwesomeIcon icon="quote-left" />
            <p id="text">
                {props.saying}
            </p>

            <p id="author">
                - {props.author}
            </p>
            <FontAwesomeIcon icon="quote-right" />
        </div>
    )
}


export default GetQuote