import React from 'react';
import './App.scss';

/* Components */
import GetQuote from './components/GetQuote'
import TweetQuote from './components/TweetQuote'
import GenerateQuote from './components/GenerateQuote'

/* FontAwesome */
import { library } from '@fortawesome/fontawesome-svg-core';
import { faTwitterSquare } from '@fortawesome/free-brands-svg-icons'
import { faQuoteLeft, faQuoteRight } from '@fortawesome/free-solid-svg-icons'


/* Data */
import bayaniQuotes from './bayaniQuotes'

/* FontAwesome Icons library */
library.add( faTwitterSquare, faQuoteLeft, faQuoteRight )

class App extends React.Component {
  constructor() {
    super()
    this.state = {
      author: "",
      saying: ""
    }

    this.generateQuote = this.generateQuote.bind(this)
  }

  componentDidMount() {
    this.generateQuote()
  }

  generateQuote() {
    const index = Math.round(Math.random() * (bayaniQuotes.length - 1))
    this.setState({
      author: bayaniQuotes[index].author,
      saying: bayaniQuotes[index].quote
    })
  }

  render() {
    const { author, saying } = this.state
    return (
      <div id="quote-box">
        <GetQuote author={author} saying={saying} />
        <TweetQuote author={author} saying={saying} />
        <GenerateQuote generate={this.generateQuote} />
      </div>
    )
  }
}

export default App;
